.Toggle{
    width: 50px;
    height: 25px;
    border-radius: 20px;
    border: 1px solid #999;
    background-color: white;
    position: fixed;
    top: 10px;
    right: 10px;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: space-around;

}

.ToggleIcon{
    width: 15px;
    height: 15px;
}

.Togglebtn{
    width: 50%;
    height: 100%;
    border-radius: 50%;
    background-color: #999;
    position: absolute;
    cursor: pointer;
}
